<template>
  <div class="card min-vh-50">
    <div class="card-header pb-0">
      <div class="d-flex justify-content-between">
        <div class="d-flex gap-4 align-items-center">
          <h4>Organizers</h4>
          <div v-if="tableProgress.loading" class="spinner-border spinner-border" role="status"></div>
        </div>
        <button-component
          color="success"
          size="sm"
          variant="outline"
          :isLoading="tableProgress.loading"
          :isDisabled="tableProgress.loading"
          @click="toggleAddModal()"
        >
          Add Organizer
        </button-component>
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table v-if="!tableProgress.loading" class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Name</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">
                Description
              </th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">
                Status
              </th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">
                Create Date
              </th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">
                Update Date
              </th>
              <th class="text-secondary opacity-7"></th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="organizer in organizers" :key="organizer.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <!-- :src="'http://testingnbackend.somee.com' + organizer.imageUrl" -->
                    <img src="../../assets/img/team-2.jpg" class="avatar avatar-sm me-3" alt="user1" />
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ organizer.name }}</h6>
                    <p class="text-center text-xs text-secondary mb-0">{{ organizer.slogan }}</p>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-center text-xs font-weight-bold mb-0">{{ organizer.description }}</p>
              </td>
              <td class="align-middle text-center text-sm">
                <span
                  class="badge badge-sm"
                  :class="{
                    'bg-gradient-secondary': organizer.isDisabled,
                    'bg-gradient-success': !organizer.isDisabled,
                  }"
                >
                  {{ organizer.isDisabled ? 'Disabled' : 'Active' }}
                </span>
              </td>
              <td>
                <p class="text-center text-xs font-weight-bold mb-0">{{ getFormattedDate(organizer.createAt) }}</p>
              </td>
              <td>
                <p class="text-center text-xs font-weight-bold mb-0">{{ getFormattedDate(organizer.updateAt) }}</p>
              </td>
              <td class="align-middle text-center">
                <a class="btn btn-link text-dark" @click="toggleEditModal(organizer.id)">
                  <i class="fas fa-pencil-alt text-dark me-2" aria-hidden="true"></i>Edit
                </a>
              </td>
              <td class="align-middle">
                <a class="btn btn-link text-danger text-gradient" @click="toggleDeleteModal(organizer.id)">
                  <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <AddOrganizer v-if="modals.addOrganizer" />
    <EditOrganizer v-if="modals.editOrganizer" />
    <DeleteOrganizer v-if="modals.deleteOrganizer" />
  </div>
</template>

<script>
  import moment from 'moment';
  import ButtonComponent from '@/components/Button.vue';
  import { defineAsyncComponent } from 'vue';

  export default {
    name: 'organizers-table',
    props: { organizers: Object },

    components: {
      ButtonComponent,
      AddOrganizer: defineAsyncComponent(() => import('./organizers-actions/add-organizer')),
      EditOrganizer: defineAsyncComponent(() => import('./organizers-actions/edit-organizer')),
      DeleteOrganizer: defineAsyncComponent(() => import('./organizers-actions/delete-organizer')),
    },

    computed: {
      tableProgress() {
        return this.$store.state.OrganizersStore.progress.organizersList;
      },
      modals() {
        return this.$store.state.OrganizersStore.modals;
      },
    },

    methods: {
      getFormattedDate: (date) => moment(date).format('YYYY-MM-DD HH:mm'),

      toggleAddModal() {
        this.$store.commit('OrganizersStore/TOGGLE_ADD_ORGANIZER_MODAL');
      },

      toggleDeleteModal(organizerId) {
        this.$store.commit('OrganizersStore/SET_ORGANIZER_ID', organizerId);
        this.$store.commit('OrganizersStore/TOGGLE_DELETE_ORGANIZER_MODAL');
      },

      toggleEditModal(organizerId) {
        this.$store.commit('OrganizersStore/SET_ORGANIZER_ID', organizerId);
        this.$store.commit('OrganizersStore/TOGGLE_EDIT_ORGANIZER_MODAL');
      },
    },
  };
</script>
