<template>
  <div>
    <OrganizersTable :organizers="organizersList.data" />
    <PaginationComponent
      v-if="organizersList.totalCount"
      class="mt-4"
      :pageCount="Math.ceil(organizersList.totalCount / organizersList.pageSize)"
      :perPage="pagination.pageSize"
      :value="currentPage"
      :align="'center'"
      @input="handlePageChange"
    />
  </div>
</template>

<script>
  import OrganizersTable from './organizers-table';
  import PaginationComponent from '@/components/Pagination';
  export default {
    name: 'organizers-page',
    components: { OrganizersTable, PaginationComponent },
    data() {
      return {
        pagination: { pageSize: 10, pageNumber: 1, currentPage: 1 },
      };
    },

    mounted() {
      this.$store.dispatch('OrganizersStore/loadOrganizersList', {
        pageNumber: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
      });
    },
    computed: {
      organizersList() {
        return this.$store.state.OrganizersStore.organizersList;
      },
    },
    methods: {
      handlePageChange(newPage) {
        this.currentPage = newPage;
        this.$store.dispatch('OrganizersStore/loadOrganizersList', {
          pageNumber: this.pagination.pageNumber * this.currentPage,
          pageSize: this.pagination.pageSize,
        });
      },
    },
  };
</script>
